@font-face {
    font-family: 'xeofficial';
    src: url('fonts/xeofficial.eot');
}
@font-face {
    font-family: 'xeofficial';
    src: url("data:application/x-font-ttf;charset=utf-8;base64,") format('truetype');
    font-weight: normal;
    font-style: normal;
}

i.xo {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'xeofficial' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

